import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Plugins'
    },
    children: [
      {
        path: 'calendar',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\calendar\\calendar.module')['CalendarInitModule']);  });})
      },
      {
        path: 'datatable',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\datatable\\datatable.module')['DatatableInitModule']);  });})
      },
      {
        path: 'draggable-cards',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\draggable-cards\\draggable-cards.module')['DraggableCardsModule']);  });})
      },
      {
        path: 'loading-buttons',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\loading-buttons\\loading-buttons.module')['LoadingButtonsModule']);  });})
      },
      {
        path: 'notifications',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\notifications\\notifications.module')['NotificationsModule']);  });})
      },
      {
        path: 'spinners',
        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\spinners\\spinners.module')['SpinnersModule']);  });})
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PluginsRoutingModule {}



